<template>
  <ApolloQuery
    :query="require('../graphql/queries/getAdminProfile.gql')"
    class="row ma-sm-4"
  >
    <template v-slot="{ result: { error, data }, isLoading }">
      <!-- Loading -->
      <div
        v-if="isLoading === 1"
        class="mx-auto"
      >
        <v-progress-circular
          indeterminate
          color="primary"
        />
      </div>

      <!-- Error -->
      <AppNotification
        v-else-if="error"
        :message="error.message"
      />

      <!-- Result -->
      <template v-else-if="data">
        <v-col v-if="!data.getAdminProfile.permissions.includes('WITHDRAW_ADMINISTRATION') && data.getAdminProfile.role !== 'SUPERUSER'">
          <v-alert
            outlined
            type="error"
          >
            Этот раздел вам недоступен!
          </v-alert>
        </v-col>

        <template v-else>
          <v-col cols="12">
            <v-menu
              ref="menu"
              v-model="menu"
              :close-on-content-click="false"
              :return-value.sync="dates"
              transition="scale-transition"
              offset-y
              min-width="290px"
            >
              <template v-slot:activator="{ on }">
                <v-text-field
                  v-model="dateRangeText"
                  solo
                  flat
                  hide-details="auto"
                  background-color="#464646"
                  label="Даты создания"
                  readonly
                  class="rounded-lg"
                  v-on="on"
                >
                  <template v-slot:prepend-inner>
                    <v-icon class="mr-1">
                      mdi-calendar
                    </v-icon>
                  </template>
                </v-text-field>
              </template>
              <v-date-picker
                v-model="dates"
                no-title
                scrollable
                range
              >
                <v-spacer />

                <v-btn
                  text
                  color="primary"
                  @click="menu = false"
                >
                  Отмена
                </v-btn>
                <v-btn
                  text
                  color="primary"
                  @click="saveDates"
                >
                  ОК
                </v-btn>
              </v-date-picker>
            </v-menu>
          </v-col>

          <v-col>
            <ApolloQuery
              :query="require('../graphql/queries/getWithdraws.gql')"
              :variables="{
                dateFrom,
                dateTo,
              }"
            >
              <template v-slot="{ result: { error: withdrawsError, data: withdrawsData }, isLoading: withdrawsIsLoading }">
                <!-- Error -->
                <AppNotification
                  v-if="withdrawsError"
                  :message="withdrawsError.message"
                />

                <!-- Result -->
                <v-card
                  v-else-if="withdrawsData || withdrawsIsLoading === 1"
                  :loading="withdrawsIsLoading === 1"
                  flat
                  class="px-4 pb-2"
                >
                  <template v-if="withdrawsData">
                    <v-card-title class="py-7">
                      Выводы: {{ withdrawsData.getWithdraws.count }}
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :items="withdrawsData.getWithdraws.withdraws"
                      :page.sync="page"
                      :items-per-page="itemsPerPage"
                      disable-sort
                      no-data-text="Выводы отсутствуют"
                      hide-default-footer
                    >
                      <template v-slot:top>
                        <ApolloMutation
                          :mutation="require(accept ? '../graphql/mutations/acceptWithdraw.gql' : '../graphql/mutations/denyWithdraw.gql')"
                          :variables="{
                            id,
                            reason,
                          }"
                        >
                          <template v-slot="{ mutate, loading, error: acceptOrDenyError }">
                            <v-dialog
                              v-model="dialog"
                              persistent
                              max-width="400px"
                            >
                              <v-card :loading="loading">
                                <v-card-title>{{ dialogTitle }}</v-card-title>
                                <v-card-text v-if="!accept">
                                  <v-text-field
                                    v-model="reason"
                                    label="Причина"
                                  />
                                </v-card-text>
                                <v-card-actions>
                                  <v-spacer />

                                  <v-btn
                                    text
                                    color="primary"
                                    @click="dialog = false"
                                  >
                                    Отмена
                                  </v-btn>
                                  <v-btn
                                    text
                                    color="error"
                                    @click="mutate()"
                                  >
                                    {{ buttonText }}
                                  </v-btn>
                                </v-card-actions>
                              </v-card>
                            </v-dialog>
                            <AppNotification
                              v-if="acceptOrDenyError"
                              :message="acceptOrDenyError.message"
                              :icon-disabled="true"
                            />
                          </template>
                        </ApolloMutation>
                      </template>

                      <template v-slot:[`item.reason`]="{ item }">
                        {{ item.reason || 'Отсутствует' }}
                      </template>

                      <template v-slot:[`item.createdAt`]="{ item }">
                        {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
                      </template>

                      <template v-slot:[`item.action`]="{ item }">
                        <v-btn
                          depressed
                          class="text-none ma-1"
                          large
                          height="36"
                          @click.stop="denyWithdraw(item)"
                        >
                          Отклонить
                        </v-btn>
                        <v-btn
                          depressed
                          color="primary"
                          class="text-none ma-1"
                          large
                          height="36"
                          @click.stop="acceptWithdraw(item)"
                        >
                          Одобрить
                        </v-btn>
                      </template>
                    </v-data-table>
                    <v-divider />

                    <div class="text-center pt-3">
                      <v-pagination
                        v-model="page"
                        :total-visible="9"
                        :length="Math.ceil(withdrawsData.getWithdraws.count / itemsPerPage)"
                      />

                      <v-select
                        v-model="itemsPerPage"
                        :items="[5, 10, 15]"
                        label="Выводов на странице"
                        class="pa-4 pt-5"
                        @change="firstPage"
                      />
                    </div>
                  </template>
                </v-card>

                <!-- No result -->
                <AppNotification
                  v-else
                  message="Нет результата :("
                />
              </template>
            </ApolloQuery>
          </v-col>
        </template>
      </template>

      <!-- No result -->
      <AppNotification
        v-else
        message="Нет результата :("
      />
    </template>
  </ApolloQuery>
</template>

<script>
import AppNotification from '@/components/AppNotification.vue';

export default {
  components: {
    AppNotification,
  },
  data() {
    return {
      menu: false,
      dates: [],
      dateFrom: '',
      dateTo: '',
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'User ID:', value: 'userId', class: 'white--text text-subtitle-2' },
        { text: 'Сумма:', value: 'amount', class: 'white--text text-subtitle-2' },
        { text: 'Валюта:', value: 'currency', class: 'white--text text-subtitle-2' },
        { text: 'Способ оплаты:', value: 'payway', class: 'white--text text-subtitle-2' },
        { text: 'Статус:', value: 'status', class: 'white--text text-subtitle-2' },
        { text: 'Статус платежа:', value: 'paymentStatus', class: 'white--text text-subtitle-2' },
        { text: 'Причина отклонения:', value: 'reason', class: 'white--text text-subtitle-2' },
        { text: 'Дата создания:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      page: 1,
      itemsPerPage: 5,
      dialog: false,
      accept: false,
      id: '',
      reason: '',
    };
  },
  computed: {
    dateRangeText() {
      return this.dates.join(' ~ ');
    },
    dialogTitle() {
      return this.accept ? 'Одобрить заявку на вывод' : 'Отклонить заявку на вывод';
    },
    buttonText() {
      return this.accept ? 'Одобрить' : 'Отклонить';
    },
  },
  methods: {
    saveDates() {
      this.$refs.menu.save(this.dates);
      [this.dateFrom, this.dateTo] = this.dates;
    },
    denyWithdraw(item) {
      this.dialog = true;
      this.accept = false;
      this.id = item.id;
    },
    acceptWithdraw(item) {
      this.dialog = true;
      this.accept = true;
      this.id = item.id;
      this.reason = '';
    },
    firstPage() {
      this.page = 1;
    },
  },
};
</script>
